import React from "react";
import TechTab from "./TechTab";

export default function TechTabs({ skills }) {
  return (
    <div className="mt-4 grid md:grid-cols-2">
      {skills.map((s, i) => (
        <TechTab key={i} name={s.technology} exp={s.experience} starFilled={s.rating} />
      ))}
    </div>
  );
}
