import React, { useState } from "react";

export default function ContactForm() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [description, setDescription] = useState("");

  const clickHandler = (e) => {
    e.preventDefault();
    if (!name || !email || !description) {
      alert("Fill all the fields");
    }
    alert("This feature is in progress");
  };

  return (
    <form className="flex flex-col w-3/4 md:w-1/3">
      <input
        type="text"
        name="name"
        id="name"
        placeholder="Enter name"
        className="p-4 my-4 focus:outline-none rounded"
        onChange={(e) => setName(e.target.value)}
      />
      <input
        type="text"
        name="email"
        id="email"
        placeholder="Enter email"
        className="p-4 my-4 focus:outline-none rounded"
        onChange={(e) => setEmail(e.target.value)}
      />
      <textarea
        type="textarea"
        name="description"
        id="description"
        placeholder="Enter description"
        className="p-4 my-4 focus:outline-none rounded"
        onChange={(e) => setDescription(e.target.value)}
      />
      <button
        onClick={clickHandler}
        className="focus:outline-none py-3 bg-pink-500 text-xl text-white rounded-xl my-4"
      >
        Submit
      </button>
    </form>
  );
}
