import TechTabs from "../components/TechTabs";
import React, { useContext, useEffect, useState } from "react";
import ThemeContext from "../store/theme-context.js";

export default function Skills() {
  const { nightTheme } = useContext(ThemeContext);

  const [skills, setSkills] = useState([]);
  useEffect(async () => {
    try {
      const response = await fetch("https://iamsvkr.github.io/data.json");
      const data = await response.json();
      setSkills(data.skills);
    } catch (error) {
      console.log("error: ", error);
    }
  }, []);

  return (
    <div className="mt-8">
      <div className="flex flex-col items-center">
        <h1 className="text-5xl font-semibold">Skills</h1>
        <h3 className={`text-xl mt-4 ${nightTheme ? "" : "text-gray-600"}`}>
          My technical level
        </h3>
      </div>
      <TechTabs skills={skills} />
    </div>
  );
}
